/// reflects documentation at https://github.com/dart-lang/test/blob/master/pkgs/test/doc/json_reporter.md
export function isSuiteEvent(event) {
    return event.type === 'suite';
}
export function isGroupEvent(event) {
    return event.type === 'group';
}
export function isTestStartEvent(event) {
    return event.type === 'testStart';
}
export function isTestDoneEvent(event) {
    return event.type === 'testDone';
}
export function isErrorEvent(event) {
    return event.type === 'error';
}
export function isDoneEvent(event) {
    return event.type === 'done';
}
export function isMessageEvent(event) {
    return event.type === 'print';
}
