export function slug(name, options) {
    const slugId = name
        .trim()
        .replace(/_/g, '')
        .replace(/[./\\]/g, '-')
        .replace(/[^\w-]/g, '');
    const id = `user-content-${slugId}`;
    // When using the Action Summary for display, links must include the "user-content-" prefix.
    const link = options.useActionsSummary ? `#${id}` : `#${slugId}`;
    return { id, link };
}
